@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300&display=swap);
:root {
  --primary-white: #fff;
  --primary-green: green;
  --primary-dark: rgba(255, 255, 255, 0);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
}

body {
  font-family: "Roboto Condensed", sans-serif;
}

.logo {
  width: 3rem;
}

nav a.nav-link {
  font-size: 0.8rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #fff !important;
  color: var(--primary-white) !important;
  border-top: 0.1875rem solid rgba(255, 255, 255, 0);
  border-top: 0.1875rem solid var(--primary-dark);
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 3rem !important;
}

.navbar-nav > li > a.active,
.navbar-nav > li > a.active:focus {
  border-top: 0.1875rem solid green;
  border-top: 0.1875rem solid var(--primary-green);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid green;
  border-top: 0.1875rem solid var(--primary-green);
}

.header-wrapper {
  position: relative;
  background-color: #923cb5;
  background-image: linear-gradient(147deg, #923cb5 0%, #000000 74%);

  background-size: cover;
  background-position: center;
  height: 95vh;
}

.navbar {
  z-index: 2;
}
.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: "Roboto Condensed", sans-serif;
}

.main-info h1 {
  font-family: "Source Code Pro", monospace;
  color: #fff;
  color: var(--primary-white);
  padding-bottom: 5rem;
}

.main-info h2 {
  font-family: "Source Code Pro", monospace;
  color: #fff;
  color: var(--primary-white);
}

.typed-text {
  font-size: 2rem;
  color: #fff;
  color: var(--primary-white);
}

@media (max-width: 768px) {
  .main-info h1,
  h2 {
    font-size: 1.5rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }
}

.btn-main-offer {
  border: 2px solid #fff;
  border: 2px solid var(--primary-white);
  text-transform: uppercase;
  border-radius: 0;
  color: white;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  margin: 2rem;
  background-color: transparent;
  font-size: large;
  z-index: 2;
}

.btn-main-offer:hover {
  border: 2px solid green;
  color: white;
  background-color: black;
  transition: 0.2s ease-in-out;
  z-index: 2;
}

.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media (max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

.about-me {
  background-color: #e84393;
  background-image: linear-gradient(315deg, #e84393 0%, #000000 74%);
  height: 100%;
}

.about-me::after {
  content: "";
  display: table;
  clear: both;
}

.about-me-img img {
  background-color: #90ee90;
  float: left;
  box-shadow: -1px -1px 1px rgb(75, 74, 74);
  padding: 2px;
  margin-top: 5%;
  margin-bottom: 5%;
  margin-left: 5%;
  margin-right: 5%;
  display: block;
  height: 90%;
  width: 30%;
  border-radius: 50%;
}

.about-me-text p,
h2 {
  float: left;
  width: 50%;
  padding: 10px;
  font-family: "Roboto Slab", serif;
  color: white;
  background-color: rgba(255, 255, 255, 0);
  background-color: var(--primary-dark);
  text-align: center;
}

.about-me-text h2 {
  margin-top: 5%;
}

.about-me-text p {
  font-size: 20px;
}

.about-me-text a {
  padding: 5%;
  color: #90ee90;
}

.about-me-text a:hover {
  color: aliceblue;
  font-size: 1rem;
  transition: 0.2s ease-out;
}

@media screen and (max-width: 768px) {
  .about-me-img img {
    width: 90%;
    height: 90%;
  }
  .about-me-text p,
  h2 {
    width: 100%;
    text-align: center;
  }
}

.contact-me-background {
  background-color: #fff;
  text-align: center;
  height: 100%;
  overflow: hidden;
}

.contact-me-background::after {
  content: "";
  display: table;
  clear: both;
}

.contact-me-form {
  display: inline-block;
  position: relative;
  background-color: #00bf24;
  width: 30%;
  text-align: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-left: 5%;
  margin-top: 7%;
  margin-bottom: 5%;
  border-radius: 25px;
}

.contact-me-form h3 {
  font-weight: 900;
  text-align: left;
  padding-top: 5%;
  padding-left: 5%;
  color: white;
  font-family: "Source Code Pro", monospace;
}

@media screen and (max-width: 768px) {
  .contact-me-form {
    width: 80%;
    height: 85%;
  }
}

.form-group {
  text-align: left;
  padding-left: 5%;
  padding-right: 5%;
}

.email-id {
  color: #fff;
  padding-top: 3%;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: bolder;
  font-size: larger;
}

.contact-me-background img {
  width: 50%;
  padding-left: 5%;
  margin-bottom: 40%;
}

.btn {
  margin-top: 5%;
  width: 90%;
  margin-bottom: 5%;
}

.btn:hover {
  width: 75%;
  font-size: large;
}

.form-control {
  margin-top: 3%;
}

.form-text {
  color: #fff;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 900;
  margin-left: 5%;
}

@media screen and (max-width: 768px) {
  .contact-me-background img {
    display: none;
  }
}

.service-page {
  margin: auto;
  width: 100%;
  height: 90vh;
  background-color: #000000;
}

